import { useContent } from '@vue-storefront/shopify';
import { onSSR } from '@vue-storefront/core';

export default {
  name: 'ContactUs',
  transition: 'fade',
  setup() {
    const {
      loading: contextsLoading,
      content,
      search
    } = useContent('pageContents');
    onSSR(async () => {
      await search({ slug: 'who-we-are', ContentType: 'page' });
    });
    return {
      content,
      contextsLoading
    };
  }
};
